@import 'main';
// End Of Import

// Start Header




/*
=====
LODEARS
=====
*/
html, body {
    // overflow-y: hidden;
    overflow-x: hidden;
}

ul {
  margin: 0;
  padding: 0;
}
a {
    text-decoration: none;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    &:hover, &:focus {
        text-decoration: none;
        color: inherit;
    }
}
button {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 0;
}


.pre-loader {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;

    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 230px;
    }

    img {
        // animation: fade-scale-up-down 1.1s infinite alternate;
    }


    @-webkit-keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    
    @keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    .progress-bar {
      background-color: #d3d3d3;
      border: none;
      // border-radius: 60px;
      overflow: hidden;
      width: 200px;
      height: 2px;
      box-shadow: none;
      margin: 42px 0 0 0;
      .Shape_1 {
        background-color: #d3d3d3;
        position: absolute;
        left: 583px;
        top: 459px;
        width: 200px;
        height: 2px;
        z-index: 4;
      }
      
    }
    .progress-bar span {
      display: block;
    }
    
    .bar {
      // background: rgba(0, 0, 0, 0.075);
      background-color: #d3d3d3;
      box-shadow: none;
    }
    
    .progress {
      -webkit-animation: loader 8s ease infinite;
              animation: loader 8s ease infinite;
      background: #f03b32;
      border: none;
      // border-radius: 50px;
      color: #fff;
      padding: 0px;
      width: 0;
      height: 2px;
    }
    
    .progress-bar {
      // -webkit-transform: rotateY(180deg);
      //         transform: rotateY(180deg);
    }
    
}
@keyframes fade-scale-up-down {
    0% {
        opacity: 0;
        transform: scale(0.987) translateY(-50%);
    }
    100% {
        opacity: 1;
        transform: scale(1.1) translateY(0%);
    }
}





  

 
.owl-carousel .owl-item img {
    width: auto;
}







.search-container {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: #00000080;
  .close {
    opacity: 0.9;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #f03b32;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .search {
    width: 50%;
    height: 50px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      padding: 0 20px;
      box-shadow: 0px 0px 10px 0px rgba(240, 60, 51, 0.5);
    }
    &::after {
      content: "";
      font-family: "Linearicons-Free";
      font-size: 18px;
      color: rgb(15, 15, 15);
      position: absolute;
      top: 50%;
      right: 20px;
      transition: all .5s ease-in-out;
      transform: translateY(-50%);
    }
  }
}




/*------ navbar start ------*/
.nav-bar {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  background: transparent;
  padding: 45px 0 0 0;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .logo-container {
    width: 260px;
  }
  .icons {
    width: 260px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    button {
      font-size: 22px;
      color: rgb(255, 255, 255);
      position: relative;
      padding: 0;
      margin: 0 23px 0 0;
      transition: all .3s ease-in-out;
      &:last-child {
        margin: 0;
      }
      &:hover {
        color: #f03b32;
      }
    }
    .menu-icon {
      display: none;
    }
    .cart-icon {
      .counter {
        position: absolute;
        top: -9px;
        right: -7px;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background-color: rgb(240, 59, 50);
        color: rgb(255, 255, 255);
        font-family: ar-reg;
        font-size: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .5s ease-in-out;
      }
      &:hover {
        .counter {
          transform: rotateY(360deg);
        }
      }
    }
  }
  .lang-switcher {
    font-family: s-bold;
    font-size: 13px;
    color: #fff;
    margin: 0 30px 0 0;
    position: relative;
    span {
      cursor: pointer;
      transition: all .3s ease-in-out;
      &::after {
        content: "\f0d7"; /* FontAwesome Unicode */
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 600;
        font-size: 10px;
        color: rgba(255, 255, 255, 0.502);
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
    }
    .switch {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 30px;
      left: 50%;
      transform: translateX(-55%);
      width: 90px;
      background-color: rgb(255, 255, 255);
      color: #1a1a1a;
      border-radius: 5px;
      padding: 5px 0;
      transition: all .5s ease-in-out;
      a {
        display: block;
        text-align: center;
        margin: 5px 0;
        transition: all .3s ease-in-out;
        &:hover {
          color: #f03b32;
        }
        &:nth-of-type(2) {
          font-family: ar-bold;
        }
      }
    }
    &:hover {
      span {
        color: #f03b32;
        &::after {
          color: #f03b32;
        }
      }
      .switch {
        top: 20px;
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

/*------ navbar end ------*/



/*------ navlist start ------*/
.nav-list {
  .list {
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      font-family: s-bold;
      font-size: 15px;
      color: #fff;
      text-transform: uppercase;
      margin: 0 15px;
      transition: all .3s ease-in-out;
      &:hover {
        color: #f03b32;
      }
    }
  }
}
/*------ navlist end ------*/





/*------ header start ------*/
.header {
    .owl-carousel {
      position: relative;
      .owl-nav {
        .owl-prev, .owl-next {
          outline: none;
          width: 60px;
          height: 60px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          font-size: 15px;
          background-color: rgba(0, 0, 0, 0.502);
          // border-radius: 50%;
          color: rgb(255, 255, 255);
          transition: all .5s ease-in-out;
          &:hover {
            color: #1a1a1a;
            background-color: #fff;
          }
        }
        .owl-prev {
          right: 0px;
        }
        .owl-next {
          left: 0px;
        }
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: 70px;
        transform: translateX(-50%);
        .owl-dot {
          width: 9px;
          height: 9px;
          background-color: #fff;
          // border-radius: 50%;
          outline: none;
          margin: 0 5px;
          &.active {
            background-color: #f03b32;
          }
        }
      }
    }
   
}

/*------ header end ------*/



.fixed-icon {
  position: fixed;
  right: 25px;
  bottom: 50px;
  z-index: 9;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: rgb(13, 187, 66);
  color: rgb(255, 255, 255);
  font-size: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .3s ease-in-out;
  button {
    color: #fff;
  }
  &:hover {
    background-color: #f03b32;
  }
}





/*------ category start ------*/
.cats {
  margin: 55px 0 95px 0;
  .cat {
    position: relative;
    overflow: hidden;
  }
  .img-container {
    img {
      transition: all .5s ease-in-out;
    }
    &:hover {
      img {
        transform: scale(1.05);
      }
    }
  }
  .btn {
    position: absolute;
    left: 50%;
    bottom: 50px;
    transform: translateX(-50%);
    width: 150px;
    height: 45px;
    border-radius: 0;
    background-color: rgb(255, 255, 255);
    color: rgb(30, 67, 125);
    font-family: md;
    font-size: 18px;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .3s ease-in-out;
    &:hover {
      background-color: #f03b32;
      color: #fff;
    }
  }
  .owl-carousel {
    position: relative;
    .owl-nav {
      .owl-prev, .owl-next {
        outline: none;
        width: 30px;
        height: 30px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        font-size: 24px;
        color: rgb(153, 153, 153);
        transition: all .5s ease-in-out;
        &:hover {
          color: #f03b32;
        }
      }
      .owl-prev {
        right: -52px;
      }
      .owl-next {
        left: -52px;
      }
    }
    .owl-dots {
      display: none;
      width: max-content;
      position: absolute;
      left: 50%;
      bottom: -30px;
      transform: translateX(-50%);
      .owl-dot {
        width: 9px;
        height: 9px;
        background-color: #fff;
        // border-radius: 50%;
        outline: none;
        margin: 0 5px;
        &.active {
          background-color: #f03b32;
        }
      }
    }
  }
}
/*------ category end ------*/




/*------ new arrival start ------*/
.new-arrival {
  .new-arrival-header {
    font-size: 26px;
    color: rgb(30, 67, 125);
    text-transform: uppercase;
    text-align: center;
    margin: 0 0 30px 0;
  }
  .selector {
    .select {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 0 40px 0;
      li {
        font-family: md;
        font-size: 14px;
        color: #8a8a8a;
        text-transform: uppercase;
        margin: 0 20px;
        transition: all .3s ease-in-out;
        &:hover {
          color: #f03b32;
        }
        &.active {
          color: #f03b32;
        }
      }
    }
  }
  .owl-carousel {
    position: relative;
    .owl-nav {
      display: block;
      .owl-prev, .owl-next {
        outline: none;
        width: 30px;
        height: 30px;
        position: absolute;
        top: 35%;
        transform: translateY(-50%);
        font-size: 24px;
        color: rgb(153, 153, 153);
        transition: all .5s ease-in-out;
        &:hover {
          color: #f03b32;
        }
      }
      .owl-prev {
        right: -52px;
      }
      .owl-next {
        left: -52px;
      }
    }
    .owl-dots {
      display: none;
      width: max-content;
      position: absolute;
      left: 50%;
      bottom: -30px;
      transform: translateX(-50%);
      .owl-dot {
        width: 9px;
        height: 9px;
        background-color: #fff;
        // border-radius: 50%;
        outline: none;
        margin: 0 5px;
        &.active {
          background-color: #f03b32;
        }
      }
    }
  }
}
/*------ new arrival end ------*/



/*--- card start ---*/
.cards {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
  .card {
    background-color: rgb(255, 255, 255);
    // width: 330px;
    overflow: hidden;
    &:hover {
      img {
        transform: scale(1.05);
      }
    }
    .card-img {
      margin: 0 0 0px 0;
      overflow: hidden;
      img {
        transition: all .5s ease-in-out;
      }
    }
    .offer {
      position: absolute;
      top: 0;
      left: 0px;
      width: 55px;
      height: 22px;
      background-color: rgb(240, 59, 50);
      color: rgb(255, 255, 255);
      font-family: ar-bold;
      font-size: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .card-body {
      padding: 0 0px;
    }
    .colors {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 15px 0;
      .color-border {
        margin: 0 3px;
        width: 24px;
        height: 24px;
        border-style: solid;
        border-width: 1px;
        border-color: rgb(204, 204, 204);
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        .color {
          width: 12px;
          height: 12px;
          background-color: #000;
          border-radius: 50%;
        }
        .blue-1 {
          background-color: rgb(136, 215, 196);
        }
        .blue-2 {
          background-color: rgb(59, 141, 214);
        }
        .blue-3 {
          background-color: rgb(34, 59, 116);
        }
        .yellow-1 {
          background-color: rgb(227, 189, 44);
        }
        .black-1 {
          background-color: rgb(49, 49, 49);
        }
        .brown-1 {
          background-color: rgb(107, 89, 51);
        }
        .green-1 {
          background-color: rgb(64, 158, 48);
        }
        .pirple-1 {
          background-color: rgb(248, 97, 130);
        }
        .gray-1 {
          background-color: rgb(224, 211, 205);
        }
      }
    }
    .title {
      text-align: center;
      font-family: md;
      font-size: 13px;
      color: rgb(0, 0, 0);
    }
    .price {
      font-family: e-bold;
      font-size: 15px;
      color: rgb(30, 67, 125);
      margin: 10px 0 0px 0;
      display: flex;
      justify-content: center;
      align-items: center;
      .old {
        font-family: reg;
        font-size: 13px;
        color: #828282;
        text-decoration: line-through;
        margin: 0 5px;
      }
    }
  }

/*--- card end ---*/





