/*------ footer start ------*/
.footer {
  margin: 70px 0 0 0;
  padding: 70px 0 50px 0;
  border-top: 1px solid #e6e6e6;
  .sp-footer {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 0 0 0px 0;
  }
  .list-header {
    margin: 0px 0 25px 0;
    font-family: s-bold;
    font-size: 15px;
    color: #000;
    text-transform: uppercase;
  }
  ul {
    font-family: reg;
    font-size: 14px;
    color: rgb(126, 126, 126);
    li {
      margin: 15px 0;
    }
    a {
      font-family: inherit;
      font-size: inherit;
      color: inherit;
      transition: all .5s ease-in-out;
      &:hover {
        padding: 0 0;
