  .fixed-menu .lang-switcher {
    font-family: s-bold;
    font-size: 13px;
    color: #444;
    margin: 0 30px 0 0;
    position: relative;
    span {
      cursor: pointer;
      transition: all .3s ease-in-out;
      &::after {
        content: "\f0d7"; /* FontAwesome Unicode */
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 600;
        font-size: 10px;
        color: rgba(0, 0, 0, 0.702);
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
    }
    .switch {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 30px;
      left: 50%;
      transform: translateX(-55%);
      width: 90px;
      background-color: rgb(255, 255, 255);
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
      color: #444;
      border-radius: 5px;
      padding: 5px 0;
      transition: all .5s ease-in-out;
      z-index: 9;
      a {
        display: block;
        text-align: center;
        margin: 5px 0;
        transition: all .3s ease-in-out;
        &:hover {
          color: #f03b32;
        }
        &:nth-of-type(2) {
          font-family: ar-bold;
        }
      }
    }
    &:hover {
      span {
        color: #f03b32;
        &::after {
          color: #f03b32;
        }
      }
      .switch {
        top: 20px;
        opacity: 1;
        visibility: visible;
      }
    }
  }

  .fixed-menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 999;
    .menu {
      width: 260px;
      height: 100%;
      position: absolute;
      top: 0;
      left: -280px;
      background-color: #fff;
      padding: 20px 20px;
      overflow-y: auto;
      transition: all .5s ease-in-out;
      &.show {
        left: 0;
      }
    }
    .menu-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .close {
        opacity: 1;
        color: #444;
        font-size: 17px;
        position: absolute;
        top: 20px;
        right : 15px;
      }
    }
    .main-list {
      margin: 35px 0px;
      transform: translateX(-50%);
      transition: all .5s ease-in-out;
      .slide {
        cursor: pointer;
        position: relative;
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: #0f0f0f;
          position: absolute;
          top: 8px;
          right: 10px;
          transition: all .5s ease-in-out;
        }
        &.active {
          color: #f03b32;
          &::after {
            content: "";
          }
        }
      }
      li {
        font-family: ar-md;
        font-size: 16px;
        color: #444;
        margin: 7px 0;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
      &.show {
        transform: translateX(0%);
      }
    }
    .sub-list {
      padding: 5px 10px;
      display: none;
      .slide-two {
        cursor: pointer;
        position: relative;
        
        &::after {
          content: "";
