          top: 9px;
          right: 15px;
          transition: all .5s ease-in-out;
        }
        &.active {
          color: #000;
          &::after {
            content: "";
          }
        }
      }
      li {
        font-family: ar-reg;
        font-size: 16px;
        color: #444;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
